<template lang="html">
    <entering-form-layout>
        <div class="authentication__form">
            <!-- signing login section -->
            <div>
                <!-- signing login section -->
                <div class="signin-section form-block">
                    <div class="form-block__header">
                        <h2 class="form-block__title">
                            Register
                        </h2>
                        <p class="form-block__description">
                            <router-link
                                class="colored-text"
                                :to="{ name: 'login', params: {} }"
                            >
                                Log in
                            </router-link> if you are an existing user.
                        </p>
                    </div>

                    <div class="form-block__body">
                        <form-input
                            v-model="$v.firstname.$model"
                            legend="Firstname"
                            prepend-icon="AccountIcon"
                            type="text"
                            name="firstname"
                            :field.sync="firstname"
                            :errors="validationMessage($v.firstname)"
                            :is-valid="$v.firstname.$dirty && !$v.firstname.$anyError"
                            @input.native="$v.firstname.$touch();"
                            @blur="$v.firstname.$touch();"
                        />
                        <form-input
                            v-model="$v.lastname.$model"
                            legend="Lastname"
                            type="text"
                            name="lastname"
                            :field.sync="lastname"
                            :errors="validationMessage($v.lastname)"
                            :is-valid="$v.lastname.$dirty && !$v.lastname.$anyError"
                            @input.native="$v.lastname.$touch();"
                            @blur="$v.lastname.$touch();"
                        />
                        <form-input
                            v-model="emailValue"
                            legend="Email address"
                            prepend-icon="EmailOutlineIcon"
                            type="text"
                            name="email"
                            :field.sync="emailValue"
                            :errors="validationMessage($v.email)"
                            :is-valid="$v.email.$dirty && !$v.email.$anyError"
                            @input.native="$v.email.$touch()"
                        />
                        <div
                            v-show="$v.email.$params.emailExists"
                            class="forgot-alert"
                        >
                            <information />
                            <div class="forgot-alert__content">
                                <span class="forgot-alert__content__text">Existing users can</span>
                                <router-link
                                    :to="{ name: 'forgot-password', params: {} }"
                                    class="forgot-alert__content__link"
                                >
                                    reset password here
                                </router-link>
                            </div>
                        </div>
                        <form-input
                            v-model="$v.password.$model"
                            legend="Password"
                            prepend-icon="LockIcon"
                            :append-icon="showPassword ? 'EyeIcon' : 'EyeOffIcon'"
                            :type="showPassword ? 'text' : 'password'"
                            name="password"
                            :style="{marginBottom: password.length > 7 ? '12px' : '30px'}"
                            :field.sync="password"
                            :errors="validationMessage($v.password)"
                            :is-valid="$v.password.$dirty && !$v.password.$anyError"
                            @clicked="showPassword = !showPassword"
                            @input.native="$v.password.$touch()"
                            @blur="$v.password.$touch()"
                        />
                        <progress-linear
                            v-if="password.length > 7"
                            :style="{marginBottom: '30px'}"
                            :validate="$v.password.$params.passwordStrengthTest"
                        />
                        <phone-number
                            @phoneValidated="validatePhone"
                        />
                        <!-- <span>error message: {{ validationMessage($v.phoneValidated) }}</span> -->
                        <button
                            class="btn-base btn-base_colored btn-base_normal button-width-100"
                            :class="{'btn-base_disabled': !isFormValid}"
                            @click="validate"
                        >
                            <template v-if="loading">
                                <button-loader />
                            </template>
                            <span v-else>Register me</span>
                        </button>
                    </div>
                    <div class="form-block__footer" />
                    <!-- <social-login mode="login" class="my-3"></social-login> -->
                </div>
                <!-- signing login section -->
            </div>
            <!-- signing login section -->
        </div>
    </entering-form-layout>
</template>

<script>
// event bus
import { eventBus } from '@/helpers/event-bus/'

// components
import EnteringFormLayout from '@/layouts/EnteringFormLayout';
import ButtonLoader from '@/components/common/ButtonLoader.vue'
import PhoneNumber from '@/components/common/form-elements/phone/PhoneNumber';

// validation
import { validationMixin } from 'vuelidate'
import formValidationMixin from '@/mixins/formValidationMixin'
import passwordValidationMixin from '@/mixins/passwordValidationMixin'
import { validationMessage } from 'vuelidate-messages'
import { validationRules } from '@/validation/register/Rules'
import { formMessages } from '@/validation/register/Messages'

// import SocialLogin from '@/components/common/SocialLogin';
// icons
import Information from 'mdi-vue/Information.vue'

// types
import {
    ACTION_CLIENT_CHECK_EMAIL,
    ACTION_CREATE_CLIENT,
    ACTION_LOGIN_CLIENT
} from '@/store/modules/client/action-types'

// vuex bindings
import {
    createNamespacedHelpers,
    mapState
} from 'vuex'
import {
    mapClientFormFields
} from '@/store/modules/checkout/index.js'
import { mapClientFields } from '@/store/modules/client'

import { debounce } from 'lodash'

const { mapActions: mapClientActions } = createNamespacedHelpers('client')

export default {
    name: 'Register',
    components: {
        EnteringFormLayout,
        PhoneNumber,
        ButtonLoader,
        Information
    },
    mixins: [
        validationMixin,
        formValidationMixin,
        passwordValidationMixin
    ],
    validations: {
        ...validationRules
    },
    data: () => ({
        emailValue: '',
        env: {
            application_name: process.env.APP_NAME,
            application_url: `${process.env.APP_URL}/`
        },
        showPassword: false,
        loading: false,
        phoneValidationStatus: false
    }),
    computed: {
        ...mapClientFormFields([
            'form_data_client.firstname',
            'form_data_client.lastname',
            'form_data_client.email',
            'form_data_client.password'
        ]),
        ...mapClientFields([
            'clientid'
        ]),
        ...mapState('phone', {
            started: 'started',
            verified: 'verified'
        }),
        isFormValid() {
            return !this.$v.$invalid && this.verified
        }
    },
    watch: {
        emailValue: debounce(function (newEmail) {
            this.email = newEmail
        }, 300)
    },
    mounted() {
        this.emailValue = this.email
        if (this.$route.query.loc) {
            window.localStorage.setItem('last_location', decodeURIComponent(this.$route.query.loc))
        }
        if (this.clientid) {
            this.$router.push({ name: 'account' })
        }
    },
    methods: {
        ...mapClientActions([
            ACTION_CREATE_CLIENT,
            ACTION_CLIENT_CHECK_EMAIL,
            ACTION_LOGIN_CLIENT
        ]),
        async checkEmail(value) {
            await this[ACTION_CLIENT_CHECK_EMAIL](value)
        },
        validationMessage: validationMessage(formMessages),
        validatePhone(value) {
            this.phoneValidationStatus = value
        },
        validateForm() {
            return this.$v.$invalid
        },
        clearForm() {
            this.$v.$reset()
            this.$store.commit('phone/RESET')
        },
        async submit() {
            const vm = this
            try {
                vm.loading = true
                await this[ACTION_CREATE_CLIENT]()
                eventBus.$emit('showSnackBar', 'Your account has been created', 'success')
                // notify google
                // vm.recordEventRegister()

                try {
                    // login client
                    vm.loading = true
                    await this[ACTION_LOGIN_CLIENT]({
                        email: vm.email,
                        password: vm.password
                    })
                    // console.log(response);
                    eventBus.$emit('showSnackBar', 'You are logged in', 'success')

                    // notify google
                    // vm.recordEventLoginFollowingRegistration()
                    // clear form
                    vm.clearForm()
                    // route visitor into account
                    if (!this.$route.query.path) {
                        vm.$router.push({
                            name: 'account'
                        })
                    } else {
                        vm.$router.push(this.$route.query.path)
                    }
                } catch (err) {
                    eventBus.$emit('showSnackBar', 'Cannot create account. Please contact support', 'error')
                } finally {
                    vm.loading = false
                }
            } catch (err) {
                console.log(err)
                // eventBus.$emit('showSnackBar', err.response.data.message, 'error')
            } finally {
                vm.loading = false
            }
        }
    }
}
</script>
